.arrowdown {    
    animation: shake-animation2 1s ease infinite;
    margin-top: 1000;
    position: absolute;
    bottom: 0;
    cursor: pointer;
  }
  
  @keyframes shake-animation2 {
    0%, 100% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-15px);
    }
  }
  