@font-face {
    font-family: 'LogoMyqueias';
    src: url('../../fonts/perpetua.ttf') format('embedded-opentype'), /* Internet Explorer */
         url('../../fonts/perpetua.ttf') format('woff2'),             /* Super Modern Browsers */
         url('../../fonts/perpetua.ttf') format('woff'),              /* Pretty Modern Browsers */
         url('../../fonts/perpetua.ttf') format('truetype'),          /* Safari, Android, iOS */
         url('../../fonts/perpetua.ttf') format('svg');               /* Legacy iOS */
}

.fontPerpetua {
    font-family: 'LogoMyqueias' !important;
}